<template>
    <v-data-table
            @update:options="onUpdate"
            :headers="headers"
            :items="items"
            show-expand
            style="height: 100%"
            :server-items-length="totalLength"
            :footer-props="{
                'items-per-page-text': ':',
                'items-per-page-all-text': ''
            }"
            no-data-text=""
            dense
            disable-sort
    >

        <template v-slot:footer.page-text="{pageStart,
  pageStop,
  itemsLength}">
            {{''+pageStart+''+pageStop+''+itemsLength+''}}
        </template>
        <template v-slot:item.user="{ item }">
            {{item.user}}
        </template>

        <!--<template v-slot:item.type="{ item }">
            <role-selector :role="item.type" @change="item.type = $event"/>
        </template>-->

        <template v-slot:item.details="{ item }">
            <v-btn icon @click="$router.push('/antiqueDetail/'+item.id)">
                <v-icon>mdi-dots-horizontal-circle-outline</v-icon>
            </v-btn>
        </template>

        <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" style="padding: 0;margin: 0;">
                <verification-stepper :antique="item" :key="rerenderKey" @error="rerenderKey++"
                                      @success="rerenderKey++"/>
            </td>
        </template>
    </v-data-table>
</template>

<script>
    import {AntiqueClient} from "../../client/AntiqueClient";
    import VerificationDetail from "./VerificationDetail";
    import VerificationStepper from "./VerificationStepper";

    export default {
        name: "VerificationTable",
        components: {VerificationStepper},
        props:{
            keyWord: String,
            date: Array
        },
        data:()=>({
            headers:[{
                text: '',
                align: 'start',
                sortable: true,
                value: 'name',
            },
                { text: '', value: 'id' },
                { text: '', value: 'userName' },
                { text: '', value: 'desp' },
                { text: '', value: 'details',sortable: false },
                { text: '', value: 'data-table-expand' }],
            items:[],
            rerenderKey:0,
            totalLength:0
        }),
        watch:{
        },
        methods:{
            onUpdate:async function(val) {
                this.$emit('load')
                if(/[^\s]+/.test(this.keyWord)) {
                    this.items.length = 0
                    const res = (await AntiqueClient.searchAntique(this.keyWord, val.page - 1, val.itemsPerPage, this.date[0], this.date[1]))
                    this.totalLength = res.totalElements
                    this.items.push(...res.content)
                }else{
                    this.items.length = 0
                    const res = (await AntiqueClient.getAntique(val.page - 1, val.itemsPerPage, this.date[0], this.date[1]))
                    this.totalLength = res.totalElements
                    this.items.push(...res.content)
                }
                this.$emit('loaded')
            },
            replaceArr(arr,n){
                arr.length  = 0;
                arr.push(...n)
            }
        }
    }
</script>

<style scoped>

</style>
